<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process252"></a>

<p><br></p>
<p><span class="rvts17">试炼场（</span><span class="rvts50">Proving Ground</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:619,Y:601</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：格科斯沃（</span><span class="rvts20">Ghechswol</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">击败巨型石头导体，取得胜利</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1392.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">位于神学院的一处传送门，可以把我们带到神学院的试炼场。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1393.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">使用</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">，可以找到试炼场原主人格科斯沃，它是一只老鹰。使用主角与他进行对话，并告诉他你在欢乐堡，浮木城都获得过冠军，他会感到很惊奇并让你挑战试炼场中的巨型石头导体。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1394.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts19">打败他们是相当容易的事情。注意三点小窍门：</span>
<br><span class="rvts20">1. </span><span class="rvts21">可以偷偷进入石头守卫的背后，拿掉他们身上的</span><span class="rvts20"></span><span class="rvts21">充能导体</span><span class="rvts20"></span><span class="rvts21">，之后他们就会像电器失去电池一样，瞬间挂掉。</span>
<br><span class="rvts20">2. </span><span class="rvts21">石头守卫由于属于机械电能怪物，因此对水元素抗性出奇的差。使用冰雹技能可以有效的对抗这些</span><span class="rvts20"></span><span class="rvts21">导电</span><span class="rvts20"></span><span class="rvts21">怪物。（使用终极水元素技能</span><span class="rvts20"></span><span class="rvts21">冰雹风暴</span><span class="rvts20"></span><span class="rvts21">可以瞬间秒掉大</span><span class="rvts20">BOSS</span><span class="rvts21">）</span>
<br><span class="rvts20">3. </span><span class="rvts21">只要杀掉大</span><span class="rvts20">BOSS</span><span class="rvts21">，所有的小守卫也会瞬间死亡。</span>
<br><span class="rvts21">打败巨型石头怪后，找到老鹰谈话，他会夸你做的很棒，并鼓励你挑战王者竞技场，成为最后唯一的神。（然而该支线任务到此依然无法关闭，</span><span class="rvts20">BUG</span><span class="rvts21">？）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



